/**
 * Calendly Booking Details Block - Editor Styles
 */

.wp-block-eightyfourem-calendly-booking-details {
	padding: 2rem;
	margin: 2rem 0;
}

.wp-block-eightyfourem-calendly-booking-details .calendly-booking-details__placeholder {
	padding: 2rem;
	text-align: center;
	background: rgba(0, 0, 0, 0.02);
	border: 2px dashed rgba(0, 0, 0, 0.2);
	border-radius: 4px;
}

.wp-block-eightyfourem-calendly-booking-details .calendly-booking-details__placeholder p {
	margin: 0.5rem 0;
	color: rgba(0, 0, 0, 0.6);
}
